export const Notification_character_chatroom_receive_img_cmp = ({src}) => {
    return (
        <div style={{ width: '39px', height: '50px', overflow: 'hidden', position: 'relative' }}>
            {src ? (
                <img src={src} alt="Selected" style={{ width: '100%', height: '100%', objectFit: 'contain' }} />
            ) : (
                <svg width="39" height="50" viewBox="0 0 40 50" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <rect x="1.80994" y="1" width="37" height="48" rx="18.5" fill="white" stroke="#FFE187" strokeWidth="2"/>
                </svg>
            )}
        </div>
    );
};

